
import { defineComponent, reactive } from 'vue';
import Network from '@/components/Network.vue'; // @ is an alias to /src
import { useRoute } from 'vue-router'; 

export default defineComponent({
  name: 'Tree',
  components: {
    Network,
  },
  setup(){
    //data
    const data = reactive({
      panel_fmid:"",
      url_pathTop:"",
      defect_fmid:""
    })

    const route = useRoute();

    data.panel_fmid = (route.params.fmid) as string;
    data.url_pathTop = "tree";
    data.defect_fmid = (route.params.defect_fmid) as string;

    return{
      data
    }
  }
});
